<template>
    <el-dialog
        :model-value="$props.loading"
        :title="$props.title"
        width="500"
        inline
        :before-close="canceEdit"
    >
        <el-form :inline="true" :model="initData" class="demo-form-inline">
            <template v-for="item in structure" :key="item.label">
                <el-form-item :label="item.label" :label-width="(item.labelWidth || 100) + 'px'">
                    <el-input
                        v-model="initData[item.prop]"
                        :placeholder="item.placeholder"
                        clearable
                        :style="item.style"
                    />
                </el-form-item>
            </template>
        </el-form>
        <template #footer>
            <div class="dialog-footer">
                <el-button @click="canceEdit">取消</el-button>
                <el-button type="primary" @click="saveEdit"> 确定 </el-button>
            </div>
        </template>
    </el-dialog>
</template>

<script lang="ts" setup>
import { nextTick, onMounted, reactive, ref, watch } from 'vue'
import type { Prop } from './index'

const props = defineProps<Prop>()

const emits = defineEmits(['canceEdit', 'saveEdit'])

// 初始数据
let initData = ref<any>({})

// 取消
const canceEdit = (): void => {
    emits('canceEdit', false)
}
// 确定
const saveEdit = () => {
    emits('saveEdit', initData.value)
}

// 数据初始化
// onMounted(() => {
//     console.log(props.rowData)
//     initData.value = { ...props.rowData }
// })

watch(
    () => props.rowData,
    () => {
        initData.value = { ...props.rowData }
    }
)
</script>
